.menu-mob-btn {
  display: none;
  position: absolute;
  top: 24px;
  right: 15px;
  border: none;
  outline: none;
  background: none;
}
@media only screen and (max-width: 1040px) {
  .menu-mob-btn {
    display: block;
    right: -6px;
  }
}
@media only screen and (max-width: 500px) {
  .menu-mob-btn {
    right: 15px;
  }
}
.menu-mob-btn span {
  width: 30px;
  height: 3px;
  margin: 12px 0;
  background-color: #333;
  display: block;
  position: relative;
  transition: all 0.3s;
}
.menu-mob-btn span:before, .menu-mob-btn span:after {
  content: " ";
  display: block;
  background-color: #333;
  left: 0;
  width: 30px;
  height: 3px;
  position: absolute;
  transition: all 0.3s;
}
.menu-mob-btn span:before {
  top: -9px;
}
.menu-mob-btn span:after {
  top: 9px;
}

